import { api as index } from "..";

const ENDPOINTS = process.env.NEXT_PUBLIC_ENDPOINT;

const api = index.injectEndpoints({
  endpoints: (build) => ({
    getOldTeachers: build.query<
      OLD_TEACHERS.GetOldTeachersResponse,
      OLD_TEACHERS.GetOldTeachersRequest
    >({
      query: () => ({
        url: `${ENDPOINTS}/old-teachers/`,
        method: "GET",
      }),

      providesTags: ["old_teachers"],
    }),
    getOldDetTeacher: build.query<
      OLD_TEACHERS.GetOldDetTeachersResponse,
      OLD_TEACHERS.GetOLdDetTeachersRequest
    >({
      query: (id) => ({
        url: `${ENDPOINTS}/old-teachers/${id}`,
        method: "GET",
      }),
    }),
  }),
});

export const { useGetOldTeachersQuery, useGetOldDetTeacherQuery } = api;
